  methods: {
    editCustomer (form) {
      this.$router.push({ name: 'customer_edit', query: { id: form.id } })
    },
    async loadData () {
      try {
        const {
          data: { data: CUSTOMER }
        } = await this.$http.get(
          `/api/customers/${this.id}`
        )
        this.form.id = CUSTOMER.id
        this.form.code = CUSTOMER.code
        this.form.customer_name = CUSTOMER.customer_name
        this.form.customer_address = CUSTOMER.customer_address
        this.form.setting_master_customer_id = CUSTOMER.setting_master_customer_id
        if (CUSTOMER.customer_contacts[0]) {
          this.form.customer_contacts = CUSTOMER.customer_contacts[0].value
        }
        this.form.customer_id = CUSTOMER.customer_contacts[0].customer_id
        this.form.customer_branch = CUSTOMER.customer_branch
      } catch (error) {
        throw error
      }
    },
    async loadCustomerlevel () {
      try {
        const { data } = await this.$http.get(`/api/setting/master/customer/${this.form.setting_master_customer_id}`)
        this.form.name_th = data.data.name_th
        this.form.name_en = data.data.name_en
      } catch (error) {
        throw error
      }
    }
  },
  async mounted () {
    this.id = this.$route.query.id
    await this.loadData()
    await this.loadCustomerlevel()
  }
}
